

@import "fonts.scss",
        "colors.scss",
        "universal.scss",
        "navigation.scss",
        "scrollbar.scss",
        "locations.scss",
        "listings.scss",
        "faq.scss",
        "about-us.scss";


//CONTENT

.content {
  height: 100vh;
  overflow: hidden;
}

div.content-map {
  max-width: 100vw;
  min-height: calc(100vh - 100px);
  position: relative;
  top: 100px;
  overflow: hidden;

  .map-page {
    width: 65vw;
    min-height: 1000px;
    height: calc(100vh - 90px);
    position: absolute;
    top: 0;
    left: 0;
    overflow: scroll;
  }
}

.left-container {
  max-width: 65vw;
  // min-width: 1000px;
  height: 130%;
  position:relative;
  object-fit: contain;
  // overflow: none;
  cursor: grab;

  &:active {
  cursor: grabbing;
  }

  img.map-img {
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }
}


.icon-container {
  position: absolute;

  .icon-container-content {
    position: relative;
    right: 12px;
    bottom: 10px;
    display: flex;
    flex-flow: nowrap;
    align-items: center;

    input[type=radio] {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      width: 0;
      height: 0;
      appearance: none;
      -webkit-appearance: none;
      -moz-appearance: none;
    }

    img {
      max-height: 25px;
      // transform: translate(-20%, 0%);
    }

    .icon-normal {
      position: relative;
      z-index: 1;
    }

    .icon-hover {
      max-height: 30px;
      position: relative;
      right: 27px;
      opacity: 0;
      cursor: pointer;
      transition: ease .2s;
      z-index: 1;
    }

    p {
      background: white;
      border-radius: 10px;
      height: 24px;
      padding: 0 8px;
      position: relative;
      right: 25px;
      top: 4px;
      white-space: nowrap;
      cursor: default;
      display: none;
      transition: ease .3s;
    }

    .icon-button {
      display: flex;
      position: relative;
      left: 8px;
    }

    .icon-hover:hover {
      opacity: 1;
    }

    .icon-hover:hover + p {
      position: relative;
      display: block;
      z-index: 999;
    }

    input[type=radio]:checked + img {
      opacity: 1;
    }

    input[type=radio]:checked + p {
      position: relative;
      display: block;
      z-index: 999;
    }
  }
}


.right-container{
  background: $light-cream;
  box-shadow: 0 0 10px 0 $dark-pink-50;
  width: 40vw;
  min-width: 300px;
  height: calc(100vh - 100px);
  // max-height: 10000px;
  padding: 20px 40px;
  position: fixed;
  right: 0;
  top: 100px;
  overflow-y: scroll;
  overflow-x: hidden;
  z-index: 9999;


  input[type=radio] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 0;
    height: 0;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
}

.base-content {
  display: block;

  h1 {
    margin: 10px 0;
  }

  p {
    margin: 20px 0;
  }

  .apartment-card-container {
    column-count: 2;

    .apartment-card {
      background: $pink-25;
      border: 4px solid $pink-05;
      border-radius: 15px;
      width: 100%;
      margin-bottom: 20px;
      object-fit: contain;
      position: relative;
      break-inside: avoid;
      transition: ease .2s;

      &:hover {
        background: $light-teal;
        border: 4px solid $light-teal;
        transform: translateY(-8px);
        cursor: pointer;
      }

      &>*:hover {
        cursor: pointer;
      }

      img {
        border-radius:12px;
        width: 100%;
        position: relative;
        top: 4px;
      }

      h3 {
        padding: 0 20px;
        margin: 10px 0;
      }

      p {
        padding: 0 20px;
        margin: 5px 0;
      }

      .totalRating {
        max-width: 185px;
        margin-left: 8px;
        margin-bottom: 15px;
      }

      .unrated {
        max-width: 155px;
        margin-left: 8px;
      }
    }
  }
}

.apartments-up {
  display: none
}

.apartment-content {
  margin: 0;
  word-break: break-word;
  display: none;

  button {
    background: none;
    border: 1px solid $dark-green-gray;
    border-radius: 20px;
    font-size: 16px;
    padding: 2px 10px;
    position: relative;
    right: 20px;
    top: 5px;
    transition: ease .2s;

    &:hover {
      // content: 'hello';
      background: $pink-50;
      border: 1px solid $light-cream;
    }
  }

  h1 {
    margin: 8px 0;
  }

  .apartment-page-icons {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0;
  }

  .apartment-content-image {
    max-width: 100%;
  }

  .apartment-content-header {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    h1 {
      margin-right: 5%;
      margin-bottom: 0;
    }

    .totalRating {
      position: relative;
      top: 5px
    }

  }
}

.apt-description {
  margin-bottom: 20px;
}

div.table-div {
  margin: 20px 0;
  display: flex;
  flex-wrap: nowrap;

  h3 {
    margin: 3px 0;
    width: 125px;
  }
}

.main-table {
  width: calc(100% - 125px);
  // max-width: 300px;
  border-collapse: collapse;

  tr:first-child {
    border-top: hidden;
  }

  tr:last-child {
    border-bottom: hidden;
  }

  tr:nth-child(odd) {
    background: $pink-25;
  }

  th, td {
    padding: 5px 16px;
    min-width: 135px;
  }

  th {
    text-align: right;
    border: 2px solid $dark-pink;
    border-left: none;
    width: 100px;
  }

  td {
    border: 2px solid $dark-pink;
    border-right: none;
  }

}

.rating {
  color: $light-cream;
  background: $teal;
  border-radius: 20px;
  padding: 4px 15px;
  margin: 4px 4px 4px 0;
  height: 100%;
}

.problems-div-div {
  max-width: calc(100% - 130px);
  min-width: 200px;
  .aaaa {
    margin: 20px 20px;
    margin-top: 10px;
  }

  a {
    text-decoration: underline;
  }
}

ul {
  margin: 0;
}

.website-link {
  text-decoration: underline;
  position: relative;
  top: 4px;
  transition: ease .3s;

  &:hover {
    color: $dark-pink;
  }
}

.not-it {
  text-decoration: underline;
  transition: ease .3s;

  &:hover {
    color: $dark-pink;
  }
}

@import "tablet.scss",
        "phone.scss";
